.actions .fa-clipboard-list {
  color: green;
}

.actions .fa-edit {
  color: blue;
}

.actions .fa-trash-alt {
  color: red;
}

.items {
  width: 100%;
  border-collapse: collapse;
}
.items .package {
  text-align: center;
}
/* Zebra striping */
.items tr:nth-of-type(odd) {
  background: #eee;
}
.items th {
  background: #333;
  color: white;
  font-weight: bolder;
}
.items td,
.items th {
  padding: 6px;
  border: 1px solid #ccc;
  text-align: left;
}

.items .s-padding {
  padding: 15px;
}
.actions {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: 20px;
}
/* 
      Max width before this PARTICULAR table gets nasty
      This query will take effect for any screen smaller than 760px
      and also iPads specifically.
      */
@media only screen and (max-width: 760px),
  (min-device-width: 768px) and (max-device-width: 1024px) {
  /* Force table to not be like tables anymore */
  .actions {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    font-size: 22px;
  }
  .items,
  .items thead,
  .items tbody,
  .items th,
  .items td,
  .items tr {
    display: block;
  }
  /* Hide table headers (but not display: none;, for accessibility) */
  .items thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }
  .items tr {
    border: 1px solid #ccc;
  }
  .items .package {
    text-align: left;
  }
  .items td {
    /* Behave  like a "row" */
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 50%;
  }
  .items td:before {
    /* Now like a table header */
    position: absolute;
    /* Top/left values mimic padding */
    top: 6px;
    left: 6px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    font-weight: bolder;
  }
  /*
   Label the data
  */
  .items td:nth-of-type(1):before {
    content: 'Event Name';
  }
  .items td:nth-of-type(2):before {
    content: 'Date';
  }
  .items td:nth-of-type(3):before {
    content: 'Time';
  }
  .items td:nth-of-type(4):before {
    content: 'Name';
  }
  .items td:nth-of-type(5):before {
    content: 'Description';
  }
  .items td:nth-of-type(6):before {
    content: 'G.A. Price';
  }
  .items td:nth-of-type(7):before {
    content: 'G.A. Qty';
  }
  .items td:nth-of-type(8):before {
    content: 'VIP Price';
  }
  .items td:nth-of-type(9):before {
    content: 'VIP Qty';
  }
  .items td:nth-of-type(10):before {
    content: 'Res. Price';
  }
  .items td:nth-of-type(11):before {
    content: 'Res. Qty';
  }
  .items td:nth-of-type(12):before {
    content: 'E.B. Price';
  }
  .items td:nth-of-type(13):before {
    content: 'E.B. Qty';
  }
  .items td:nth-of-type(14):before {
    content: 'Actions';
  }
}

